@font-face {
  font-family: 'VT323';
  font-style: normal;
  font-weight: 400;
  src: local('VT323 Regular'), local('VT323-Regular'), url("fonts/VT323-latin.woff2") format('woff2');
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2212, U+2215;
}
.VT323,
.CodeEditor ._editor .CodeMirror,
.Terminal {
  font-family: 'VT323', monospace;
}
.CodeEditor {
  height: 100%;
  width: 50%;
  border-right: solid #0f2a3f 1px;
}
.CodeEditor ._editor {
  height: 100%;
}
.CodeEditor ._editor .CodeMirror {
  height: 100%;
  font-size: 1.5em;
}
.Editor {
  height: 100%;
}
.cm-s-nyx8.CodeMirror {
  background: #08141e;
  color: #4e495f;
}
.cm-s-nyx8 div.CodeMirror-selected {
  background: #0f2a3f;
}
.cm-s-nyx8 .CodeMirror-line::-moz-selection,
.cm-s-nyx8 .CodeMirror-line > span::-moz-selection,
.cm-s-nyx8 .CodeMirror-line > span > span::-moz-selection {
  background: #f00;
}
.cm-s-nyx8 .CodeMirror-line::selection,
.cm-s-nyx8 .CodeMirror-line > span::selection,
.cm-s-nyx8 .CodeMirror-line > span > span::selection {
  background: #f00;
}
.cm-s-nyx8 .CodeMirror-line::-moz-selection,
.cm-s-nyx8 .CodeMirror-line > span::-moz-selection,
.cm-s-nyx8 .CodeMirror-line > span > span::-moz-selection {
  background: #f00;
}
.cm-s-nyx8 .CodeMirror-gutters {
  background: $background;
  border-right: 0px;
}
.cm-s-nyx8 .CodeMirror-guttermarker {
  color: #816271;
}
.cm-s-nyx8 .CodeMirror-guttermarker-subtle {
  color: #f00;
}
.cm-s-nyx8 .CodeMirror-linenumber {
  color: #f00;
}
.cm-s-nyx8 .CodeMirror-cursor {
  border-left: 1px solid #997577;
}
.cm-s-nyx8 span.cm-comment {
  color: #4e495f;
}
.cm-s-nyx8 span.cm-atom {
  color: #f6d6bd;
}
.cm-s-nyx8 span.cm-number {
  color: #f6d6bd;
}
.cm-s-nyx8 span.cm-property,
.cm-s-nyx8 span.cm-attribute {
  color: #c3a38a;
}
.cm-s-nyx8 span.cm-keyword {
  color: #816271;
}
.cm-s-nyx8 span.cm-string {
  color: #816271;
}
.cm-s-nyx8 span.cm-variable {
  color: #c3a38a;
}
.cm-s-nyx8 span.cm-variable-2 {
  color: #997577;
}
.cm-s-nyx8 span.cm-def {
  color: #f6d6bd;
}
.cm-s-nyx8 span.cm-bracket {
  color: #4e495f;
}
.cm-s-nyx8 span.cm-tag {
  color: #816271;
}
.cm-s-nyx8 span.cm-link {
  color: #f6d6bd;
}
.cm-s-nyx8 span.cm-error {
  background: #816271;
  color: #997577;
}
.cm-s-nyx8 .CodeMirror-activeline-background {
  background: #f00;
}
.cm-s-nyx8 .CodeMirror-matchingbracket {
  text-decoration: underline;
  color: #f00 !important;
}
.Output {
  height: 100%;
  width: 50%;
  position: absolute;
  top: 0;
  right: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.Output canvas {
  background: #0f2a3f;
  -ms-interpolation-mode: nearest-neighbor;
      image-rendering: -webkit-optimize-contrast;
      image-rendering: -moz-crisp-edges;
      image-rendering: -o-pixelated;
      image-rendering: pixelated;
}
.Terminal {
  font-size: 1.5em;
  width: 100%;
  padding-left: 0;
  margin: 0 0 3em 0;
}
.TerminalHistory,
.TerminalOutput {
  color: #997577;
}
.TerminalHistory em,
.TerminalOutput em {
  font-style: normal;
  color: #f6d6bd;
}
.TerminalInput {
  position: relative;
  margin: 1em 0 0 0;
}
.TerminalInput .display {
  border: none;
  background: none;
  color: #f6d6bd;
  font-size: 1em;
  width: 100%;
}
.TerminalInput .display .caret {
  -webkit-animation-name: pulse;
          animation-name: pulse;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}
.TerminalInput .display .hide {
  visibility: hidden;
}
.TerminalInput .control {
  opacity: 0;
  position: absolute;
}
@-webkit-keyframes pulse {
  0% {
    opacity: 0;
  }
  49.9% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  99.9% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes pulse {
  0% {
    opacity: 0;
  }
  49.9% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  99.9% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
html {
  background: #08141e;
}
body {
  margin: 0;
  padding: 0;
  overflow: hidden;
}
.App {
  position: absolute;
  width: 100%;
  height: 100%;
}



